import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, DollarSign, ArrowDownLeft, ArrowRightLeft, Clock, Calendar, Package } from 'lucide-react';
import { ProfitService } from '@/utils/profitService';
import { MasterPinService } from '@/utils/masterPinService';
import { reportsService } from '@/utils/reportsService';
import { MasterPinDialog } from './MasterPinDialog';
import { toast } from '@/hooks/use-toast';
import { getUserSubscription, SubscriptionPlan } from '@/utils/subscriptionService';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { db } from '@/firebase';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { userDataService } from '@/integrations/firebase/services';

interface MonthlyProfitDisplayDialogProps {
  userId?: string;
  transactions: any[];
}

interface ProfitData {
  monthlyWithdrawalProfit: number;
  monthlyTransferProfit: number;
  monthlySalesProfit: number;
  lastUpdated: Date;
}

const MonthlyProfitDisplayDialog: React.FC<MonthlyProfitDisplayDialogProps> = ({ userId, transactions }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [profitData, setProfitData] = useState<ProfitData>({
    monthlyWithdrawalProfit: 0,
    monthlyTransferProfit: 0,
    monthlySalesProfit: 0,
    lastUpdated: new Date()
  });
  const { userSubscription, user, profile } = useFirebaseAuth();

  useEffect(() => {
    if (userId && isOpen && isAuthenticated) {
      loadProfitData();
    }
  }, [userId, isOpen, isAuthenticated]);

  //        
  useEffect(() => {
    if (!isOpen || !isAuthenticated) return;
    const interval = setInterval(() => {
      const now = new Date();
      //           
      if (
        profitData.lastUpdated.getMonth() !== now.getMonth() ||
        profitData.lastUpdated.getFullYear() !== now.getFullYear()
      ) {
        loadProfitData();
      }
    }, 30000); //   30 
    return () => clearInterval(interval);
  }, [isOpen, isAuthenticated, profitData.lastUpdated]);

  const fetchMonthlySalesProfit = async (): Promise<number> => {
    try {
      if (!userId) return 0;
      
      const formatter = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' });
      const now = new Date();
      const todayCairoStr = formatter.format(now);
      const [currYearStr, currMonthStr] = todayCairoStr.split('-');

      const year = parseInt(currYearStr, 10);
      const month = parseInt(currMonthStr, 10);
      if (!year || !month) return 0;

      const lastDay = new Date(year, month, 0).getDate();
      const startStr = `${currYearStr}-${currMonthStr}-01`;
      const endStr = `${currYearStr}-${currMonthStr}-${String(lastDay).padStart(2, '0')}`;

      const mainSnap = await getDocs(query(collection(db, 'dailySales'), where('userId', '==', userId)));
      const nestedSnap = await getDocs(collection(db, 'users', userId, 'dailySales'));

      const combined: { id: string; data: any }[] = [];
      mainSnap.forEach(d => combined.push({ id: d.id, data: d.data() }));
      nestedSnap.forEach(d => combined.push({ id: d.id, data: d.data() }));

      const unique = new Map<string, any>();
      combined.forEach(item => {
        const key = String(item.data.clientId || item.id);
        if (!unique.has(key)) unique.set(key, item.data);
      });

      let salesProfit = 0;
      unique.forEach((data: any) => {
        if (data.isDeferred) return;

        let dateStrVal = '';
        if (data.date) {
          dateStrVal = String(data.date).slice(0, 10);
        } else if (data.createdAt) {
          let d: Date | null = null;
          if (typeof data.createdAt.toDate === 'function') d = data.createdAt.toDate();
          else if (data.createdAt instanceof Date) d = data.createdAt;
          else if (typeof data.createdAt === 'number') d = new Date(data.createdAt);
          else if (typeof data.createdAt === 'string') d = new Date(data.createdAt);
          if (d && !isNaN(d.getTime())) {
            const fmt = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' });
            dateStrVal = fmt.format(d);
          }
        }

        if (!dateStrVal) return;
        if (dateStrVal < startStr || dateStrVal > endStr) return;

        const qty = Number(data.quantity || 1);
        const selling = Number(data.sellingPrice ?? data.price ?? 0);
        const wholesale = Number(data.wholesalePrice ?? 0);
        const profit = Number(
          data.profit ?? (selling - wholesale) * qty
        );
        salesProfit += profit;
      });

      return Math.round(salesProfit * 100) / 100;
    } catch (e) {
      console.warn('    :', e);
      return 0;
    }
  };

  const loadProfitData = async () => {
    if (!userId) return;

    try {
      const formatter = new Intl.DateTimeFormat('en-CA', { timeZone: 'Africa/Cairo' });
      const now = new Date();
      const [currYearStr, currMonthStr] = formatter.format(now).split('-');

      const start = new Date(`${currYearStr}-${currMonthStr}-01T00:00:00`);
      const end = new Date(new Date(start.getFullYear(), start.getMonth() + 1, 0).setHours(23, 59, 59, 999));

      const monthTransactions = await userDataService.getUserTransactionsForDateRange(userId, start, end);

      const filteredStatus = monthTransactions.filter((t: any) => {
        const st = String((t as any).status || t.status || 'completed').toLowerCase();
        return st === 'completed' || st === 'confirmed' || st === 'pending';
      });

      const visibleMonthly = reportsService.filterVisibleTransactions(filteredStatus as any[], 'monthly', userId);

      let monthlyWithdrawProfit = 0;
      let monthlyTransferProfit = 0;

      visibleMonthly.forEach((t: any) => {
        const rawType = (t.type || (t as any).txnType || '').toLowerCase();
        const title = String(t.title || '');
        const isMachineWithdraw = rawType === 'machine_withdrawal' || title === '  ';

        const normalizedType =
          rawType === 'withdrawal' || rawType === 'machine_withdrawal' ? 'withdraw' : rawType;

        const isWithdraw =
          !isMachineWithdraw &&
          (normalizedType === 'withdraw' || normalizedType === 'receive');
        const isSend =
          !isMachineWithdraw &&
          (normalizedType === 'send' || normalizedType === 'transfer');

        const txForCalc = {
          type: normalizedType,
          amount: Number(t.amount || 0),
          commission: t.commission,
          fee: (t as any).fee,
        };

        const profit = ProfitService.calculateProfitFromTransaction(txForCalc);

        if (normalizedType === 'atm_withdraw') {
          monthlyWithdrawProfit += profit;
        } else if (isWithdraw) {
          monthlyWithdrawProfit += profit;
        } else if (isSend) {
          monthlyTransferProfit += profit;
        }
      });

      const monthlySalesProfit = await fetchMonthlySalesProfit();

      setProfitData({
        monthlyWithdrawalProfit: Math.round(monthlyWithdrawProfit * 100) / 100,
        monthlyTransferProfit: Math.round(monthlyTransferProfit * 100) / 100,
        monthlySalesProfit,
        lastUpdated: new Date()
      });
    } catch (error) {
      console.error('Error loading profit data:', error);
    }
  };

  const formatCurrency = (amount: number) => {
    return `${Number(amount).toLocaleString('en-US', { maximumFractionDigits: 0 })} .`;
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  //         
  const isZeroPlan = async (): Promise<boolean> => {
    try {
      const localSub: any = (userSubscription as any)?.subscription || null;
      const rawLocal: any = localSub?.planType ?? localSub?.plan ?? null;
      const planLocalStr = typeof rawLocal === 'string' ? rawLocal.toLowerCase() : null;
      if (rawLocal === SubscriptionPlan.ZERO || planLocalStr === 'zero' || planLocalStr === '0' || rawLocal === 0) {
        return true;
      }
    } catch {}

    try {
      if (userId) {
        const remoteSub: any = await getUserSubscription(userId);
        const rawRemote: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
        const planRemoteStr = typeof rawRemote === 'string' ? rawRemote.toLowerCase() : null;
        if (rawRemote === SubscriptionPlan.ZERO || planRemoteStr === 'zero' || planRemoteStr === '0' || rawRemote === 0) {
          return true;
        }
      }
    } catch {}

    return false;
  };


  const handleEyeClick = async () => {
    //         
    try {
      if (await isZeroPlan()) {
        toast({
          title: ' ',
          description: '       .     .',
          variant: 'destructive'
        })
        return
      }
    } catch {}
    
    //      
    const uid = userId || user?.uid;
    const lockKey = uid ? `monthlyReportsLock_v4_${uid}` : 'monthlyReportsLock_v4';
    const isReportsLocked = (localStorage.getItem(lockKey) ?? localStorage.getItem('monthlyReportsLock_v4')) === 'true';

    //      
    const hasPin = await MasterPinService.hasMasterPin(uid);
    
    //           
    if (isReportsLocked && hasPin) {
      setIsMasterPinDialogOpen(true);
    } else {
      setIsAuthenticated(true);
      setIsOpen(true);
    }
  }

  const handlePinSuccess = async () => {
    //            
    try {
      if (await isZeroPlan()) {
        toast({
          title: ' ',
          description: '       .     .',
          variant: 'destructive'
        });
        setIsMasterPinDialogOpen(false);
        return;
      }
    } catch {}

    setIsAuthenticated(true);
    setIsOpen(true);
    setIsMasterPinDialogOpen(false);
  };

  return (
    <>
      <Button
        size="sm"
        variant="ghost"
        onClick={handleEyeClick}
        className="h-6 w-6 p-0 text-gray-400 hover:text-purple-600 hover:bg-purple-50 rounded-full transition-colors duration-200"
        title="  "
      >
        <Eye className="h-4 w-4" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-[280px] sm:max-w-[320px] mx-auto bg-white dark:bg-[#0f172a] border border-gray-200 dark:border-slate-800 shadow-xl rounded-xl p-0 overflow-hidden">
          <DialogHeader className="bg-gray-50 dark:bg-slate-900/50 px-4 py-3 border-b border-gray-100 dark:border-slate-800">
            <DialogTitle className="text-sm font-bold text-gray-900 dark:text-slate-100 flex items-center justify-center gap-2">
              <div className="p-1.5 bg-white dark:bg-slate-800 rounded-full border border-gray-200 dark:border-slate-700">
                <Calendar className="h-4 w-4 text-purple-600 dark:text-purple-400" />
              </div>
               
            </DialogTitle>
          </DialogHeader>

          <div className="p-4 space-y-3">
            {/*  */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-red-50 dark:bg-red-900/20 rounded-md">
                  <ArrowDownLeft className="h-3.5 w-3.5 text-red-600 dark:text-red-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"></span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.monthlyWithdrawalProfit)}
              </span>
            </div>

            {/*  */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-green-50 dark:bg-green-900/20 rounded-md">
                  <ArrowRightLeft className="h-3.5 w-3.5 text-green-600 dark:text-green-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"></span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.monthlyTransferProfit)}
              </span>
            </div>

            {/*   */}
            <div className="flex items-center justify-between p-2 rounded-lg border border-gray-100 dark:border-slate-800 hover:bg-gray-50 dark:hover:bg-slate-800/50 transition-colors">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-violet-50 dark:bg-violet-900/20 rounded-md">
                  <Package className="h-3.5 w-3.5 text-violet-600 dark:text-violet-400" />
                </div>
                <span className="font-medium text-gray-700 dark:text-slate-300 text-sm"> </span>
              </div>
              <span className="font-bold text-sm text-gray-900 dark:text-slate-100">
                {formatCurrency(profitData.monthlySalesProfit)}
              </span>
            </div>

            {/*  */}
            <div className="flex items-center justify-between p-3 bg-purple-50 dark:bg-purple-900/20 rounded-lg border border-purple-100 dark:border-purple-900/30 mt-2">
              <div className="flex items-center gap-2">
                <div className="p-1.5 bg-white dark:bg-slate-800 rounded-md shadow-sm">
                  <DollarSign className="h-3.5 w-3.5 text-purple-600 dark:text-purple-400" />
                </div>
                <span className="font-bold text-purple-900 dark:text-purple-100 text-sm"></span>
              </div>
              <span className="font-bold text-base text-purple-700 dark:text-purple-300">
                {formatCurrency(profitData.monthlyWithdrawalProfit + profitData.monthlyTransferProfit + profitData.monthlySalesProfit)}
              </span>
            </div>
          </div>

          <div className="bg-gray-50 dark:bg-slate-900/50 px-4 py-3 border-t border-gray-100 dark:border-slate-800 flex justify-center">
            <Button
              onClick={() => setIsOpen(false)}
              className="bg-white border border-gray-300 text-gray-700 hover:bg-gray-50 hover:text-gray-900 w-full sm:w-auto px-8 rounded-lg text-sm font-medium shadow-sm"
            >
              
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <MasterPinDialog
        open={isMasterPinDialogOpen}
        onOpenChange={setIsMasterPinDialogOpen}
        mode="verify"
        title="    "
        description="       "
        userId={userId || user?.uid}
        userRole={profile?.role}
        onSuccess={handlePinSuccess}
      />
    </>
  );
};

export const MonthlyProfitEyeButton: React.FC<MonthlyProfitDisplayDialogProps> = (props) => {
  return <MonthlyProfitDisplayDialog {...props} />;
};

export default MonthlyProfitDisplayDialog;
